<template>
    <b-container fluid class="content">
      <b-row class="text-center">
        <b-col>
          <b-card border-variant="light" header="Notification App List" class="text-center">
            <div class="div-create-app">
              <b-button size="md" variant="outline-secondary" router-link to="/app/create"> + create app </b-button>
            </div>
            <b-card-text>
              <modal-confirm @deleteApp="deleteApp" ref="modalConfirm" :name="selectedName"></modal-confirm>
              <b-table hover :items="items" :fields="fields">
                <template slot="numbers" slot-scope="row">
                  {{ row.item.index }}
                </template>
                <template slot="name" slot-scope="row">
                  {{ row.item.name }}
                </template>
                <template slot="createAt" slot-scope="row">
                  {{ row.item.createAt}}
                </template>
                <template slot="ios" slot-scope="row">
                  <router-link :to="{ path: `app/ios-provider/${row.item.id}`}">
                  <b-button size="sm" variant="outline-secondary"> details </b-button>
                  </router-link>
                </template>
                <template slot="android" >
                  <b-button size="sm" variant="outline-secondary"> details </b-button>
                </template>
                <template slot="app" slot-scope="row">
                  <router-link :to="{ path: `app/${row.item.id}` }">
                  <b-button size="sm" variant="outline-secondary" class="mr-1"> edit</b-button>
                  </router-link>
                  <b-button
                    size="sm"
                    variant="outline-danger"
                    @click="confirmDelete(`${row.item.id}`, `${row.item.name}`)">
                    delete
                  </b-button>
                </template>
              </b-table>
            </b-card-text>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
</template>

<script>
import ModalConfirm from '@/components/ModalConfirm';
const stringUtil = require('../Utils/string-formater');

export default {
  name: 'home',
  components: {
    ModalConfirm
  },
  methods: {
    deleteApp () {
      this.axios
        .post(process.env.VUE_APP_SERVICE_HOST + '/app/delete', { id: this.selectedId })
        .then(response => {
          this.getListApp();
        })
        .catch(error => {
          this.info = error.message;
        });
    },
    edit () {
      //
    },
    setDefault () {
      this.selectedId = '';
      this.selectedName = '';
      this.items = [];
    },
    getListApp () {
      this.setDefault();
      this.axios
        .get(process.env.VUE_APP_SERVICE_HOST + '/app/list')
        .then(response => {
          // map response to items
          const items = response.data;
          for (let i = 0; i < items.length; i++) {
            let item = {};
            item.index = i + 1;
            item.name = items[i].name;
            item.createAt = stringUtil.reformatDate(items[i].createdAt);
            item.id = items[i].id;
            this.items.push(item);
          }
        })
        .catch(error => {
          this.info = error.message;
        });
    },
    confirmDelete (id, name) {
      this.selectedId = id;
      this.selectedName = name;
      this.$refs.modalConfirm.showModal();
    }
  },
  data: () => {
    return {
      items: [],
      fields: [
        { key: 'numbers', label: '#' },
        { key: 'name', label: 'Name' },
        { key: 'createAt', label: 'Created At' },
        { key: 'ios', label: 'IOS' },
        { key: 'android', label: 'Android' },
        { key: 'app', label: 'app' }
      ],
      selectedName: '',
      selectedId: ''
    };
  },
  mounted () {
    this.getListApp();
  }
};
</script>

