<template>
    <b-modal ref="modal"  hide-footer hide-header>
      <div class="d-block text-center">
        <p>Confirm to delete app "{{ name }}"</p>
      </div>
      <b-button class="mt-3" size="sm" variant="outline-danger" block @click="confirm">Confirm</b-button>
      <b-button class="mt-2" size="sm" variant="outline-secondary" block @click="hideModal">Cancel</b-button>
    </b-modal>
</template>

<script>
export default {
  props: {
    name: String
  },
  methods: {
    showModal () {
      this.$refs['modal'].show();
    },
    hideModal () {
      this.$refs['modal'].hide();
    },
    toggleModal () {
    // We pass the ID of the button that we want to return focus to
    // when the modal has hidden
      this.$refs['modal'].toggle('#toggle-btn');
    },
    confirm () {
      this.$refs['modal'].hide();
      this.$emit('deleteApp');
    }
  }
};
</script>
